.header {
	background-color: rgba(#000000, 0);
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	font-size: var(--header-font-size);	

	&__wrapper {
		max-width: 1390px;
		width: 100%;
		margin-left: auto;
		padding: 28px 40px 28px 0;
		@include flex-container($justify-content: space-between, $align-items: center);		
	}
}