//color
$color-white: #ffffff;
$color-black: #000000;
$color-purple: #8824cd;
$color-dark-blue: #2e3b4e;
$color-pink: #c15065;

//background
.features {
	--background-color: #{$color-dark-blue};
}

.some-info {
	--background-color-overlay: #{rgba($color-pink, 0.6)};
}

.about {
	--background-color: #{$color-white};
}

.experience {
	--background-color-overlay: #{rgba($color-purple, 0.6)};
}

.contact {
	--background-color: #{$color-pink};
}

.footer {
	--background-color: #{$color-purple};
}

//font-family
:root {
	--main-font-family: 'Open Sans', sans-serif;
}

//font-size
:root {
	--main-big-font-size: 20px;
	--main-small-font-size: 15px;
	--title-intro-font-size: 50px;
	--header-font-size: 13px;
	--btn-font-size: 18px;
	--title-section-font-size: 40px;
}

//text color
:root {
	--main-text-color: #{$color-white};
}

.search-box,
.about {
	--text-color: #{$color-black};
}

.some-info .btn {
	--text-color: #{$color-pink};
}

//Breakpoints
$breakpoint-max-tablet-landscape: 940px;
$breakpoint-max-tablet-portrait: 768px;
$breakpoint-max-phone: 640px;