.nav {
	@include flex-container($flex-wrap: wrap, $justify-content: flex-end, $align-items: center);

	&__navbar {
		list-style: none;
		@include flex-container($align-items: center);		
	}

	&__item {
		&:not(:last-child) {
			margin-right: 60px;
		}		
	}

	&__link {
		text-decoration: none;
	}

	&__search-box{
		margin-left: 30px;
	}
}